.container.testimonials__container {
  width: 40%;
  padding-bottom: 4rem;
}

.client__avatar {
  width: 4rem;
  aspect-ratio: 1/1;
  overflow: hidden;
  border-radius: 50%;
  margin: 0 auto 1rem;
  border: 0.4rem solid #1e497d;
}

.client__avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.testimonial {
  background: var(--color-bg-variant);
  text-align: center;
  padding: 2rem;
  border-radius: 2rem;
  user-select: none;
}

.client__review {
  color: var(--color-light);
  font-weight: 300;
  display: block;
  width: 80%;
  margin: 0.8rem auto 0;
}

/*===========================MEDIA QUERIES (MEDIUM DEVICES) =========================*/
@media screen and (max-width: 1024px) {
  .container.testimonials__container {
    width: 60%;
    margin: auto;
  }
}

/*===========================MEDIA QUERIES (SMALL DEVICES) =========================*/

@media screen and (max-width: 600px) {
  .container.testimonials__container {
    width: var(--container-width-sm);
    margin: 0 1rem;
  }
  .client__review {
    width: var(--container-width-sm);
  }
}
